<template>
  <div
    :class="`bubble-warning ${bwnDirection}-bubble bubble-warning-${bwnColor}`">
    <div class="bwn-icon">
      <warning-icon />
    </div>
    <div class="bwn-txt">
      <div class="bwn-txt-title">{{ $t('settings.board.warning-nano.title') }}</div>
      <div class="bwn-txt-body">{{ $t('settings.board.warning-nano.body') }}</div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import WarningIcon from '@public/img/icons/iconos_svg_52.svg';

export default {
  components: {
    WarningIcon,
  },
  props: {
    bwnDirection: {
      type: String,
      default: 'right',
    },
  },
  data() {
    return {
      bwnColor: 'white',
    };
  },
  mounted() {
    this.bwnColor = /(dark)|(black)/.test(this.interfaceMode) ? 'dark' : 'white';
  },
  computed: {
    ...mapGetters(['interfaceMode']),
  },
  watch: {
    interfaceMode(newValue) {
      this.bwnColor = /(dark)|(black)/.test(newValue) ? 'dark' : 'white';
    },
  },
};
</script>
