<template>
  <v-btn-toggle
    v-model="boardValue"
    mandatory
    class="scs-card-board">
    <v-btn class="arduino-uno-btn" :value="boards.arduinoUno">
      <div>
        <uno-icon class="arduino-uno-icon"/>
        <div class="scs-btn-txt"> Arduino UNO </div>
      </div>
    </v-btn>
    <div class="arduino-nano-container">
      <bubble-warning-nano :class="`arduino-nano-tooltip${nanoClicked ? ' arduino-nano-tooltip-clicked' : ''}`"/>
    <v-btn class="arduino-nano-btn" :value="boards.arduinoNano">
      <div>
        <div>
          <nano-icon class="arduino-nano-icon"/>
          <div class="scs-btn-txt"> Arduino Nano </div>
        </div>
      </div>
    </v-btn>
    </div>
  </v-btn-toggle>
</template>
<script>
import { mapGetters } from 'vuex';
import UnoIcon from '@public/img/icons/settings/uno_v0.svg';
import NanoIcon from '@public/img/icons/settings/nano_v0.svg';
import BubbleWarningNano from '../actions/bubble-warning-nano.vue';

export default {
  components: {
    UnoIcon,
    NanoIcon,
    BubbleWarningNano,
  },
  props: {
    scsBoard: String,
  },
  data() {
    return {
      boardValue: this.scsBoard,
      fqbn_arduino_uno: 'arduino:avr:uno',
      fqbn_arduino_nano: 'arduino:avr:nano',
      nanoClicked: false,
      nanoProcessor: { config: { option: 'cpu', value: 'atmega328old' } },
    };
  },
  watch: {
    boardValue(newValue) {
      this.nanoClicked = newValue === this.boards.arduinoNano;
      const config = this.nanoClicked ? this.nanoProcessor : {};
      if (this.nanoClicked) {
        setTimeout(() => { this.nanoClicked = false; }, 2000);
      }
      this.$emit('update:board', { uuid: newValue, ...config });
    },
  },
  computed: {
    ...mapGetters('boards', { findBoards: 'find' }),
    boards() {
      const boards = this.findBoards({ query: { $sort: { name: 1 } } }).data;
      return {
        arduinoUno: boards.find((b) => b.fqbn === this.fqbn_arduino_uno)?.uuid,
        arduinoNano: boards.find((b) => b.fqbn === this.fqbn_arduino_nano)?.uuid,
      };
    },
  },
};
</script>
<style scoped>
.scs-card-board .arduino-uno-btn,
.scs-card-board .arduino-nano-btn {
  height: 200px !important;
  background-color: transparent;
  border: none;
  flex-direction: column;
}
.scs-card-board .arduino-uno-btn:before,
.scs-card-board .arduino-nano-btn:before {
  background-color: transparent;
  border: 2px solid currentColor;
  border-radius: 10%;
}
.scs-card-board .arduino-nano-container .arduino-nano-btn {
  margin-left: 30px;
  box-shadow: none !important;
}
.arduino-uno-btn .arduino-uno-icon {
  height: 150px !important;
}
.arduino-nano-btn .arduino-nano-icon {
  transform: rotate(90deg);
  width: 150px;
  height: 150px;
}
.arduino-nano-container .arduino-nano-tooltip {
  height: 150px;
  width: 300px;
  position: absolute;
  z-index: 1;
  visibility: hidden;
  left: -80px;
  top: 20px;
}
.arduino-nano-container:hover .arduino-nano-tooltip,
.arduino-nano-container .arduino-nano-tooltip.arduino-nano-tooltip-clicked{
  visibility: visible;
}
.scs-btn-txt {
  text-transform: none;
}
</style>
